    getOrderTrack() {
      const context = this;
      if (context.data.soNo) {
        const data = {
          deliveryOrderCode: context.data.soNo,
        }
        personalApi.getYlhLogisticsMsgList(data).then(res => {
          if (res.data.success) {
            context.data.resData = res.data.data
            context.calcMdNodeList(context.data.resData)
          }
        })
      } else {
        const data = {
          corderSn: this.data.cOrderSn,
          expressNumber: this.data.expressNumber,
          expressCompany: this.data.expressCompany
        }
        personalApi.orderProductTrack(data).then(res => {
          if (res.data.success) {
            context.data.resData = res.data.data
            context.calcNodeList(context.data.resData)
          }
        })
      }
    },
    /**
     * @description 计算展示节点
    */
    calcNodeList(trackObj) {
      let _trackList = []
      // 物流节点新加订单取消大节点【orderCancelNodes】，这个大节点顺序放在最上面，如果签收大节点【signNodes】有小节点，这个订单取消大节点就不展示了
      if (trackObj.signNodes.length === 0 && trackObj.orderCancelNodes.length > 0) {
        _trackList = _trackList.concat(this.toTrackList({ title: '已取消', node: trackObj.orderCancelNodes })) // 取消节点
      } else {
        if (trackObj.signNodes.length > 0) _trackList = _trackList.concat(this.toTrackList({ title: '已签收', node: trackObj.signNodes })) // 签收节点
      }
      if (trackObj.installNodes.length > 0) _trackList = _trackList.concat(this.toTrackList({ title: '安装服务', node: trackObj.installNodes })) // 派送节点
      if (trackObj.deliverNodes.length > 0) _trackList = _trackList.concat(this.toTrackList({ title: '派送中', node: trackObj.deliverNodes })) // 派送节点
      if (trackObj.shippingNodes.length > 0) _trackList = _trackList.concat(this.toTrackList({ title: '运输中', node: trackObj.shippingNodes })) // 运输节点
      if (trackObj.collectNodes.length > 0) _trackList = _trackList.concat(this.toTrackList({ title: '已揽件', node: trackObj.collectNodes })) // 揽收节点
      if (trackObj.depotNodes.length > 0) _trackList = _trackList.concat(this.toTrackList({ title: '仓库处理中', node: trackObj.depotNodes })) // 仓库节点
      if (trackObj.orderNodes.length > 0) _trackList = _trackList.concat(this.toTrackList({ title: '已下单', node: trackObj.orderNodes })) // 订单节点
      this.trackList = _trackList
      this.trackList.forEach(i => {
        i.nodeContent = this.selectPhoneNumber(i.nodeContent) || ''
      })
    },
    /**
     * @description 计算门店订单物流展示节点
    */
    calcMdNodeList(trackObj) {
      let _trackList = []
      if (trackObj.logistics && trackObj.logistics.length > 0) {
        trackObj.logistics.reverse().forEach(i => {
          _trackList = _trackList.concat(this.mdToTrackList(i))
        })
        this.trackList = _trackList
        this.trackList.forEach(i => {
          i.nodeContent = this.selectPhoneNumber(i.nodeContent) || ''
        })
        console.log('calcMdNodeList:')
        console.log(this.trackList)
      }
    },
    /**
     * @description 替换字符串中的电话号码
     * @param {str} 替换前字符串
     * 替换后字符串
    */
    selectPhoneNumber(str) {
      // var regx = /(1[3|4|5|7|8][\d]{9}|0[\d]{2,3}-[\d]{7,8}|400[-]?[\d]{3}[-]?[\d]{4})/g;
      var regx = /(1(3|4|5|6|7|8|9)\d{9})|(0[\d]{2,4}-[\d]{5,8})/g;
      var phoneNums = str.match(regx);
      if (phoneNums) {
        for (var i = 0; i < phoneNums.length; i++) {
          var temp = phoneNums[i]
          str = str.replace(phoneNums[i], '<a class="phone-num" style=" text-decoration: underline;color: #2878FF;">' + temp + '</a>');
        }
      }
      return str;
    },
    toTrackList(obj) {
      let list = []
      list = list.concat(obj.node)
      list.unshift({ nodeContent: '', nodeName: obj.title, nodeType: 'main', nodeTime: '2019-12-12 09:33:02' })
      return list
    },
    mdToTrackList(obj) {
      let nodeType = Object.keys(obj)[0]
      let list = []
      if (obj[nodeType] && obj[nodeType].length > 0) {
        list = list.concat(obj[nodeType])
        // obj[nodeType][0].nodeName
        list.unshift({ nodeContent: '', nodeName: this.getNodeName(nodeType), nodeType: 'main', nodeTime: '' })
      }
      return list
    },
    getNodeName(nodeType) {
      let ret = ''
      switch (nodeType) {
        case 'order':
          ret = '已下单'
          break;
        case 'depot':
          ret = '仓库处理中'
          break;
        case 'shipping':
          ret = '运输中'
          break;
        case 'deliver':
          ret = '派送中'
          break;
        case 'sign':
          ret = '已签收'
          break;
        case 'install':
          ret = '安装服务'
          break;
        case 'cancel':
          ret = '已取消'
          break;
      }
      return ret
    },
