<template>
  <div class="content">
    <div class="track-info-container" :class="{'wx':from === '1'}">
     <div v-for="(item, index) in parentData" :key="index" :class="{'line': true, 'complete': index === 0, 'first': index === 0,'main': item.nodeType==='main','hide-line':item.nodeType==='main'&&index===parentData.length-2}">
      <span :class="{'install':item.nodeName==='安装服务','collect':item.nodeName==='已揽件','cancle':item.nodeName==='已取消','signIn':item.nodeName==='已签收','delivery':item.nodeName==='派送中','transit':item.nodeName==='运输中','warehouse':item.nodeName==='仓库处理中','order':item.nodeName==='已下单'}"></span>
       <div :class="{'main-title':item.nodeType==='main','info':item.nodeType!=='main'}" v-html="item.nodeType==='main'?item.nodeName:item.nodeContent" @click="option">
      </div>
      <div v-if="trackNo && noCancle && item.nodeType==='main' && item.nodeName==='派送中'" class="view-map-container" @click="toLogistigsTrack">
        <div class="map-tips"><div class="map-icon"></div>查看配送地图</div>
      </div>
      <div class="date" v-if="item.nodeType!=='main'">
        {{item.nodeTime || ''}}
      </div>
    </div>
    </div>
      <van-dialog v-model="copyOptionShow" className="custome-confirm" :showConfirmButton="false" :showCancelButton="false" title="" width="304px" cancel-button-color="#666666" get-container="#element-container">
      <div class="option-container" :class="{'wx':from === '1'}">
        <div class="phonenum-show">{{currentPhoneNum}}</div>
        <div class="copy-option" @click="copyPhone"><span >复制号码</span></div>
        <div class="call-option">
          <div @click="callPhone">拨打电话</div>
        </div>
        <div class="cancle-option">
          <div @click="cancleOption">取消</div>
        </div>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import ClipboardJS from 'clipboard'; // 复制插件
/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';
/*
 * 追踪订单 物流信息
*/
export default {
  name: 'orderTrackInfo',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return []
      },
    },
    /**
     * 发货单号/网单号
    */
    orderNo: {
      type: String,
      default: function () {
        return ''
      },
    },
    /**
    * 来源
     */
    from: {
      type: null,
      default: function () {
        return ''
      },
    },
  },
  data() {
    return {
      copyOptionShow: false, // 复制号码，拨打电话操作弹窗
      currentPhoneNum: '', // 电话号码,
      trackNo: '', // 物流单号
    }
  },
  created() {
    this.getLogisticsCode()
  },
  computed: {
    noCancle() {
      return this.parentData.filter(i => {
        return i.nodeName === '已取消'
      }).length === 0
    }
  },
  methods: {
    /**
     * @description 根据发货单号/网单号获取运单号
    */
    getLogisticsCode() {
      personalApi.getLogisticsCode({ deliveryOrderCode: this.orderNo }).then(res => {
        if (res.data.success) {
          this.trackNo = res.data.data
        }
      })
    },
    /**
     * @description 打开电话号码操作弹窗
    */
    option(event) {
      if (event.target.className === 'copy phone-num' || event.target.className === 'phone-num') {
        console.log(event.target.innerText)
        this.currentPhoneNum = event.target.innerText
        this.copyOptionShow = true
      }
    },
    // handleInfoScroll(e) {
    //   this.$emit('handleInfoScroll', e)
    // },
    /**
     * @description 打开电话号码操作弹窗
    */
    cancleOption() {
      this.copyOptionShow = false
    },
    /**
     * @description 复制号码
    */
    copyPhone() {
      let that = this
      const clipboard = new ClipboardJS('.copy-option', {
        text: function(trigger) {
          return that.currentPhoneNum
        }
      });
      clipboard.on('success', () => {
        this.$toast.success('复制成功');
        this.copyOptionShow = false
      });

      clipboard.on('error', () => {
        this.$toast.success('复制失败');
      });
    },
    /**
     * @description  拨打电话
    */
    callPhone() {
      location.href = 'tel:' + this.currentPhoneNum + ''
      this.copyOptionShow = false
    },
    /**
     * @description 跳转物流智汇宝物流轨迹
    */
    toLogistigsTrack() {
      this.$router.push({ path: '/logistigsTrack',
        query: {
          trackNo: this.trackNo,
        } })
    },

  }
}
</script>

