<template>
  <div class="order-info-content">
    <div class="top">
      <img class="thumbnail" :src="parentData.resData.orderProductInfo.defaultImageUrl | imageToHttps" alt="商品封面图"/>
      <div class="line">
        <div>
          订单编号：
        </div>
        <div>
          {{parentData.resData.orderProductInfo.orderSn || ''}}
        </div>
      </div>
    </div>
    <div class="bottom" v-if="parentData.resData.orderProductInfo.expressNumber || parentData.resData.orderProductInfo.expressCompany || parentData.resData.orderProductInfo.expectTime">
      <div class="left">
        <img :src="$cdnImageUrl+'/order/tracking/littlecar.png'" alt="icon">
      </div>
      <div class="right">
        <div class="line" v-if="parentData.resData.orderProductInfo.expressNumber">
          <div >
            运单编号：
          </div>
          <div>
            {{parentData.resData.orderProductInfo.expressNumber || ''}}
          </div>
          <div v-if="parentData.resData.orderProductInfo.expressNumber" @click="copy" class="copy border">
            <span>复制</span>
          </div>
        </div>
        <div class="line" v-if="parentData.resData.orderProductInfo.expressCompany">
          <div>
            物流公司：
          </div>
          <div>
            {{parentData.resData.orderProductInfo.expressCompany || ''}}
          </div>
        </div>
        <div class="line" v-if="parentData.resData.orderProductInfo.expectTime">
          <div style="max-width: 5rem;">
            预计{{parentData.resData.orderProductInfo.expectTime}}送达
          </div>
        </div>
      </div>
      <div class="icon">
        <img :src="$cdnImageUrl+'/order/tracking/car.png'" alt="icon">
      </div>
    </div>
  </div>
</template>

<script>
import ClipboardJS from 'clipboard'; // 复制插件

/**
 * tost提醒
 */
import Vue from 'vue';
import { Toast } from 'vant';
Vue.use(Toast);

/*
 * 追踪订单 订单信息
*/
export default {
  name: 'trackingOrderInfo',
  components: {
  },
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function () {
        return {}
      },
    },
  },
  data() {
    return {
      /**
       * {downArrowLeft} 三角下箭头左侧坐标点
       * {downArrowTop} 三角下箭头顶侧坐标点
       * {downArrowHeight} 三角下箭头高度
       */
      data: {
        showDialog: false,
        downArrowLeft: 0,
        downArrowTop: 0,
        downArrowHeight: 0,
      },
      /**
       * {downArrowLeft} 三角下箭头左侧坐标点
       * {downArrowTop} 三角下箭头顶侧坐标点
       * {downArrowHeight} 三角下箭头高度
       */
      dataExpress: {
        showDialog: false,
        downArrowLeft: 0,
        downArrowTop: 0,
        downArrowHeight: 0,
      }
    }
  },
  mounted () {
  },
  methods: {
    /**
     * @description  展示、隐藏更多网单号码
     * @param {}
    */
    showMore () {
      const context = this;
      if (!context.parentData.checkedWDInfo.cOrderSn || context.parentData.resData.orderWorkFlowsViewA.length <= 1 || context.parentData.fromDetail) {
        return
      }
      const downArrow = document.getElementById('down-arrow');
      context.data.downArrowLeft = downArrow.offsetLeft + 10;
      context.data.downArrowTop = downArrow.offsetTop;
      context.data.downArrowHeight = downArrow.offsetHeight;
      context.data.showDialog = true;
    },

    /**
     * @description  展示、隐藏更多运单号码
     * @param {}
    */
    showMoreExpress () {
      const context = this;
      if (context.parentData.checkedWDInfo.expressList && context.parentData.checkedWDInfo.expressList.length <= 1) {
        return
      }
      const downArrow = document.getElementById('down-arrow-express');
      context.dataExpress.downArrowLeft = downArrow.offsetLeft;
      context.dataExpress.downArrowTop = downArrow.offsetTop;
      context.dataExpress.downArrowHeight = downArrow.offsetHeight;
      context.dataExpress.showDialog = true;
    },

    /**
     * @description  运单号码点击下拉，切换运单号码后的关闭回调
     * @param {invoiceNumber} 运单号码
     * @param {expressName} 物流公司
     * @param {netPointName} 网点名称
     * @param {netPointMobile} 网点电话
     * @param {showDialog} 关闭更多运单号码的弹窗
    */
    closeCallBack (data) {
      const context = this;
      context.data.showDialog = data.showDialog;
      context.$emit('changeCallBack', data);
    },

    /**
     * @description  运单号码点击下拉，切换运单号码后的关闭回调
     * @param {invoiceNumber} 运单号码
     * @param {expressName} 物流公司
     * @param {netPointName} 网点名称
     * @param {netPointMobile} 网点电话
     * @param {showDialog} 关闭更多运单号码的弹窗
    */
    expressCloseCallBack (data) {
      const context = this;
      context.dataExpress.showDialog = data.showDialog;
      context.$emit('expressChangeCallBack', data);
    },

    /**
     * @description  复制运单号码
     * @param {copyData} 要复制的运单号码
     * 返回复制提示信息
    */
    copy () {
      const context = this;
      let orderSn = context.parentData.resData.orderProductInfo.expressNumber;
      const clipboard = new ClipboardJS('.copy', {
        text: function(trigger) {
          return orderSn
        }
      });

      clipboard.on('success', () => {
        context.$toast.success('复制成功');
      });

      clipboard.on('error', () => {
        context.$toast.success('复制失败');
      });
      // const oInput = document.createElement('input');
      // oInput.value = context.parentData.checkedWDInfo.invoiceNumber;
      // document.body.appendChild(oInput);
      // oInput.select(); // 选择对象
      // document.execCommand('Copy'); // 执行浏览器复制命令
      // oInput.className = 'oInput';
      // oInput.style.display = 'none';
      // oInput.blur(); // 屏蔽默认键盘弹出；
      // Toast.success('复制成功');
    }
  },
}
</script>

